
import React, { Component } from "react";
import ProjectServices from "../projects-service";

class Opinions extends Component {
  constructor(){
    super()
    this.state = {
      opinions:[]
    }

  }

  componentDidMount(){
    ProjectServices.getOne(this.match.params.id)
      .then((project) => {
        this.setState({opinions: project})
      }) 
      console.log('id from the opinions',this.match.params.id)
  }


  render() {
    return (
     <div className="general-opinions">
      <h1>Opinions page</h1>
     </div>
    );
  }
}



export default Opinions